.login{
    background-image: url(../../Photos/bitcoin.png);
    background-size: cover;
    background-repeat: no-repeat;
    height: 100vh;
    position: relative;
    top: -1rem;

    .log-in{
        height: 100vh;
        max-height: 70vh;
        width: 26%;
        background: purple;
        opacity: 69%;
        position: relative;
        top: 7rem;
        left: 15rem;
        border-radius: 1rem;

        form{
            position: relative;
            top:2.5rem;
            text-align: left;
            left: 3rem;

         
   
        .log-2{
            position: relative;
            bottom: 10rem;
            color: yellow;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
            font-weight: bold;
            font-size: 1rem;
            text-decoration: none;

            &:hover{
                color: white;
                text-decoration: underline;                
            }
        }

            .text{
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                position: relative;
                left: -3rem;
                top: -1rem;
                text-align: center;
                color: snow;
                font-weight: bold;
                font-size: 1.7rem;
            }

            p{
                line-height: 3;

            input{
                border-radius: 1rem;
                border: none;
                padding: .5rem 2rem;
                background-color: aquamarine;
                font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                color: blue;
            }
        }

        .btn{
            padding:.5rem 2rem;
            border-radius: 1rem;
            position: relative;
            left: 3.8rem;
            color: black;
            border: none;
            font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
            font-weight: bold;

                 &:hover{
                    cursor: pointer;
                }
           }

           .other{
            width: 15em;
            height: 10em;
            position: relative;
            bottom: 2rem;
            justify-items: center;
            color: white;

            button{
                left: .5rem;
                bottom: 1rem;
            }

           :nth-child(1){
            text-align: center;
            padding-top: 1em;
            font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
           }

           :nth-child(3){
            margin-left: .3em;
           }
}

           .ask{
            position: relative;
            bottom: 7rem;
            left: -1rem;
            color: snow;
            font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
            font-weight: bold;
            font-size: 1rem;

            .log{
                color: black;
                font-size: 1.2rem;
                text-decoration: none;
                &:hover{
                    color: white;
                    text-decoration: underline;
            }
        }
           }
       
        }
    }

    .who{
        position: relative;
        width: 20rem;
        // background-color: green;
        bottom: 13rem;
        left: 43em;

        h2{
            font-weight: 900;
            color: rgb(211, 255, 14);
            font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            text-transform: uppercase;
            letter-spacing: .1em;
            line-height: 1.4;
        }

        span{
            text-transform: uppercase;
            color: snow;
            font-weight: bold;
            font-size: 2rem;
            font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;

        }
    }

}































