.navigation{
    position: sticky;
    top: 0;
    z-index: 1;
    width: 100%;
    height: 3rem;
    background-color: green;
    display: flex;
    align-items: center;
    justify-content: space-between;

    .logo{
        margin-left: 2rem;
        font-weight: 900;
        font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
        text-transform: uppercase;

        .n{
            text-decoration: none;
            color: snow;
        }
    }

    .nav-display{
   
        display: flex;
        justify-content: space-between;
        align-items: center;
        ul{
            list-style: none;
            display: flex;
           li{
            margin: .3rem;

            .n{
            text-decoration: none;
            color: snow;
        }
           }
        }
    }
 
    .btn{
        // all: unset;
        font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        font-weight: bold;
        margin: .3rem;
        padding:.5rem 2rem;
        border-radius: 1rem;
        border: none;
        color: snow;
    }
}