
.signup{
    background-image: url(../../../public/bit.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
// display: flex;
// align-items: center;
// justify-content: center;
// height: 40em;
// margin-top: 10rem;
// background-color: red;

// .rightimg{
//     border-radius: 6rem;
// }
    .sign-up{
        // transition: 1.25s;
        height: 100vh;
        max-height: 70vh;
        width: 26%;
        background: purple;
        opacity: 65%;
        position: relative;
        top: 7rem;
        left: 15rem;
        border-radius: 1rem;

        form{
            transform: rotate(3);
            transition: 0.3s ease-in-out;
            position: relative;
            top:2.5rem;
            text-align: left;
            left: 3rem;

            p{
                line-height: .5;
            }

            .sign{
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                position: relative;
                left: -3rem;
                top: -1rem;
                text-align: center;
                color: white;
                font-weight: bold;
                font-size: 1.7rem;
                line-height: 1;
            }

           input{
                    border-radius: 1rem;
                    border: none;
                    padding: .5rem 2rem;
                    background-color: aquamarine;
                    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                    color: blue;
                }
    
                .fname{
                    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                    text-align: center;
                    color: white;
                    font-weight: bold;
                    font-size: 1rem;
                }
    
                .email{
                    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                    text-align: center;
                    color: white;
                    font-weight: bold;
                    font-size: 1rem;
                }
    
                .password{
                    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                    text-align: center;
                    color: white;
                    font-weight: bold;
                    font-size: 1rem;
                }
    
                .up{
                    padding:.5rem 2rem;
                    border-radius: 1rem;
                    position: relative;
                    left: 3rem;
                    color: black;
                    border: none;
                    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
                    font-weight: bold;

                    &:hover{
                        cursor: pointer;
                    }
                }

                .ask{
                    position: relative;
                    left: -1rem;
                    color: snow;
                    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
                    font-weight: bold;
                    font-size: 1rem;

                    .log{
                        color: black;
                        font-size: 1.2rem;
                        text-decoration: none;
                        &:hover{
                            color: white;
                            text-decoration: underline;

                        }
                    }
                }

                .check{
                    color: snow;
                }
        } 
    }
}


@media screen and (max-width: 1500px) {

    .sign-up{
        height: 70vh;
        max-height: 10vh;
    }
    
}