.login {
  background-image: url(../../Photos/bitcoin.png);
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  position: relative;
  top: -1rem;
}
.login .log-in {
  height: 100vh;
  max-height: 70vh;
  width: 26%;
  background: purple;
  opacity: 69%;
  position: relative;
  top: 7rem;
  left: 15rem;
  border-radius: 1rem;
}
.login .log-in form {
  position: relative;
  top: 2.5rem;
  text-align: left;
  left: 3rem;
}
.login .log-in form .log-2 {
  position: relative;
  bottom: 10rem;
  color: yellow;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  font-weight: bold;
  font-size: 1rem;
  text-decoration: none;
}
.login .log-in form .log-2:hover {
  color: white;
  text-decoration: underline;
}
.login .log-in form .text {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  left: -3rem;
  top: -1rem;
  text-align: center;
  color: snow;
  font-weight: bold;
  font-size: 1.7rem;
}
.login .log-in form p {
  line-height: 3;
}
.login .log-in form p input {
  border-radius: 1rem;
  border: none;
  padding: 0.5rem 2rem;
  background-color: aquamarine;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: blue;
}
.login .log-in form .btn {
  padding: 0.5rem 2rem;
  border-radius: 1rem;
  position: relative;
  left: 3.8rem;
  color: black;
  border: none;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bold;
}
.login .log-in form .btn:hover {
  cursor: pointer;
}
.login .log-in form .other {
  width: 15em;
  height: 10em;
  position: relative;
  bottom: 2rem;
  justify-items: center;
  color: white;
}
.login .log-in form .other button {
  left: 0.5rem;
  bottom: 1rem;
}
.login .log-in form .other :nth-child(1) {
  text-align: center;
  padding-top: 1em;
  font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
}
.login .log-in form .other :nth-child(3) {
  margin-left: 0.3em;
}
.login .log-in form .ask {
  position: relative;
  bottom: 7rem;
  left: -1rem;
  color: snow;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  font-weight: bold;
  font-size: 1rem;
}
.login .log-in form .ask .log {
  color: black;
  font-size: 1.2rem;
  text-decoration: none;
}
.login .log-in form .ask .log:hover {
  color: white;
  text-decoration: underline;
}
.login .who {
  position: relative;
  width: 20rem;
  bottom: 13rem;
  left: 43em;
}
.login .who h2 {
  font-weight: 900;
  color: rgb(211, 255, 14);
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-transform: uppercase;
  letter-spacing: 0.1em;
  line-height: 1.4;
}
.login .who span {
  text-transform: uppercase;
  color: snow;
  font-weight: bold;
  font-size: 2rem;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}/*# sourceMappingURL=Login.css.map */