.signup {
  background-image: url(../../../public/bit.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
}
.signup .sign-up {
  height: 100vh;
  max-height: 70vh;
  width: 26%;
  background: purple;
  opacity: 65%;
  position: relative;
  top: 7rem;
  left: 15rem;
  border-radius: 1rem;
}
.signup .sign-up form {
  transform: rotate(3);
  transition: 0.3s ease-in-out;
  position: relative;
  top: 2.5rem;
  text-align: left;
  left: 3rem;
}
.signup .sign-up form p {
  line-height: 0.5;
}
.signup .sign-up form .sign {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  position: relative;
  left: -3rem;
  top: -1rem;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 1.7rem;
  line-height: 1;
}
.signup .sign-up form input {
  border-radius: 1rem;
  border: none;
  padding: 0.5rem 2rem;
  background-color: aquamarine;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: blue;
}
.signup .sign-up form .fname {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 1rem;
}
.signup .sign-up form .email {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 1rem;
}
.signup .sign-up form .password {
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 1rem;
}
.signup .sign-up form .up {
  padding: 0.5rem 2rem;
  border-radius: 1rem;
  position: relative;
  left: 3rem;
  color: black;
  border: none;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-weight: bold;
}
.signup .sign-up form .up:hover {
  cursor: pointer;
}
.signup .sign-up form .ask {
  position: relative;
  left: -1rem;
  color: snow;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande", "Lucida Sans", Arial, sans-serif;
  font-weight: bold;
  font-size: 1rem;
}
.signup .sign-up form .ask .log {
  color: black;
  font-size: 1.2rem;
  text-decoration: none;
}
.signup .sign-up form .ask .log:hover {
  color: white;
  text-decoration: underline;
}
.signup .sign-up form .check {
  color: snow;
}

@media screen and (max-width: 1500px) {
  .sign-up {
    height: 70vh;
    max-height: 10vh;
  }
}/*# sourceMappingURL=SignUp.css.map */